import tabsComponent from '../resources/tabsComponent.js';
import defaultSettings from './iatDefaultSettings.js';
import tabs from './iatTabs.js';
import {clone} from '../resources/utilities.js';
import {save} from '../../../../generator/iatGeneratorModel';
import {createNotifications} from '../../../../../utils/notifyComponent.js';
import {updateSettings} from './importComponent.js'
import {toString} from './outputComponent.js';

export default iat;

const iat = (args, external) => m.component(iatComponent, args, external);

let iatComponent = {
    controller: controller,
    view: view
};

function controller({file, study}, external = null){
    let ctrl = {
        study: study ? study : null,
        file : file ?file : null,
        err : m.prop([]),
        loaded : m.prop(false),
        notifications : createNotifications(),
        settings : clone(defaultSettings),
        external: external,
        is_locked:m.prop(study ? study.is_locked : null),

        do_save,
        is_settings_changed,
        disable_all
    }

    function load(ctrl) {
        return ctrl.file.get()
            .catch(ctrl.err)
            .then(() => {
                if (ctrl.file.content() !== '') {
                    let input = JSON.parse(ctrl.file.content());
                    console.log('input', input);
                    ctrl.settings = updateSettings(ctrl.settings, input);
                    console.log('settings', ctrl.settings);
                    ctrl.prev_settings = clone(ctrl.settings);
                }
                ctrl.loaded(true);
            })
            .then(m.redraw);
    }

    function do_save(){
        ctrl.err([]);
        ctrl.settings.output = toString(ctrl.settings); // the server takes the data from here
        save('iat', m.route.param('studyId'), m.route.param('fileId'), ctrl.settings)
            .then(ctrl.study.get())
            .then(()=>ctrl.notifications.show_success(`IAT Script successfully saved`))
            .then(m.redraw)
            .catch(err => ctrl.notifications.show_danger('Error Saving:', err.message));
        delete ctrl.settings.output; //for updating the prev_settings without the output
        ctrl.prev_settings = clone(ctrl.settings)
    }

    function is_settings_changed(){
        return JSON.stringify(ctrl.prev_settings) !== JSON.stringify(ctrl.settings);
    }
    function disable_all() {
        console.log(ctrl.is_locked());
        console.log('do', document);

        var form = document.getElementById("tabs");
        console.log(form);
        //var elements = form.elements;
        var elements = form.getElementsByTagName('*')
        console.log(elements);
        for (var i = 0, len = elements.length; i < len; ++i) {
            elements[i].readOnly = true;
        }
    }

    external ? null : load(ctrl);
    //console.log(ctrl.is_locked());
    return ctrl;
}
function view(ctrl){
    if(!ctrl.external) {
        return !ctrl.loaded()
            ?
            m('.loader')
            :
            m('.container.space',[
            m('div', ctrl.notifications.view()),
                m('h1', 'Create my IAT script', [
                    ctrl.err().length === 0 ? '' : m('.row.space.alert.alert-danger', ctrl.err()),
                    ctrl.is_locked() ? '' :
                    m('button.btn btn btn-primary', {
                    onclick: () => ctrl.do_save(),
                    disabled: !ctrl.is_settings_changed(),
                    style: {float: 'right', 'margin-top': '7px', 'margin-right': '15px'}
                    }, 'Save'),
                ]),
                m.component(tabsComponent, tabs, ctrl.settings, defaultSettings, ctrl.external, ctrl.is_locked())
    ])
    }
    return m('.container',
        m('div', ctrl.notifications.view()),
            m('h1.display-3', 'Create my IAT script'),
        m.component(tabsComponent, tabs, ctrl.settings, defaultSettings, ctrl.external)
    );


}
